/* eslint-disable no-param-reassign */
import PropTypes from 'prop-types';
import { useRef, useEffect, useState } from 'react';
import P5 from 'p5';
import { setupCanvas, handleInteractions } from '../SketchHelpers';
import Person from '../Person';
import DiseaseState from '../DiseaseState';
import Replay from '../Replay';

const Immunity = (props) => {
  const {
    backgroundColor,
    maxFrameCount,
  } = props;

  const canvasWidth = 300;
  const canvasHeight = 100;

  const containerRef = useRef();
  const [showReplay, setShowReplay] = useState(false);
  const [handleReplay, setHandleReplay] = useState();

  const sketch = (p) => {
    const population = [];

    const setupPopulation = () => {
      population[0] = new Person({
        x: 100,
        y: 48,
        horizontalSpeed: 1,
        verticalSpeed: 0,
        radius: 30,
        state: DiseaseState.INFECTED,
        p5: p,
      });

      population[1] = new Person({
        x: 170,
        y: 48,
        horizontalSpeed: 0,
        verticalSpeed: 0,
        radius: 30,
        state: DiseaseState.CURED,
        p5: p,
      });
    };

    p.setup = () => {
      setupCanvas(p, canvasWidth, canvasHeight);
      setupPopulation();
    };

    p.draw = () => {
      if (p.frameCount === maxFrameCount) {
        p.noLoop();
        setShowReplay(true);
      }

      p.background(backgroundColor);

      population.forEach((person) => {
        person.move();
        handleInteractions(p, person, population);
        person.render();
      });
    };
  };

  useEffect(() => {
    // eslint-disable-next-line no-new
    new P5(sketch, containerRef.current);
  });

  return (
    <div id="immunity" ref={containerRef} className={showReplay && 'overlay'} style={{ maxWidth: canvasWidth, position: 'relative' }}>
      <Replay show={showReplay} onClick={handleReplay} />
    </div>
  );
};

Immunity.propTypes = {
  backgroundColor: PropTypes.string,
  maxFrameCount: PropTypes.number,
};

Immunity.defaultProps = {
  backgroundColor: '#f8f9fc',
  maxFrameCount: 300,
};

export default Immunity;
